a.link__regular {
	color: $color__link;

	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

.li__no-bullet {
	list-style-type: none;
}

.li__bullet {
	list-style-type: inherit;
}
